import Pdfh5 from "pdfh5";
export default {
    name:'LookProjectFile',
    data(){
        return{
            pdfh5: null
        }
    },
    mounted(){
        this.$emit('setHeaderShow', {blod:true,title:''});
        this.pdfh5 = new Pdfh5("#demo", {
            pdfurl: "./test.pdf" 
        });
        //
        this.pdfh5.on("complete", function (status, msg, time) {
            console.log("" + status + "" + msg + "" + time + "" + this.totalNum)
        })
    },
    destroyed(){
    },
    methods:{
    }
}



